<!--公用组件：
 /**
  * @desc Test
  * @module @/components
  * @desc 组件描述：
  * @author zkhq
  * @date 2020/3/31 17:41
  * @param {Object} [title]    - 参数说明
  * @param {String} [columns] - 参数说明
  * @example 调用示例
  *  <hbTable :title="title" :columns="columns" :tableData="tableData"></hbTable>
  */
-->
<template>
    <div class="app-main">
      <div class="app-main-content">

        <div class="app-container-item">
          <div class="app-form">
            <div class="app-form-item">
              <el-select v-model="componentNow" placeholder="请选择">
                <el-option
                  v-for="item in components"
                  :key="item.id"
                  :label="item.name"
                  :value="item.url">
                </el-option>
              </el-select>
            </div>
            <div class="app-form-item">
              <BaseTheme></BaseTheme>
            </div>
          </div>
        </div>

        <div class="app-container-item" style="height: 50vh">
          <component :is="componentNow" class="full-all"></component>
        </div>

      </div>
    </div>
</template>

<script>
// import
import BaseTheme from '@/components/theme/BaseTheme'
import Table from '@/pages/Main/Plugin/Table'
import {apiRouter} from '@/api'
// import {objC} from '@/utils/common'
export default {
  name: 'Test',
  data () {
    return {
      // 定义的每个data 属性必须有注释
      // 注释规则： //
      // 当前菜单
      componentNow: '',
      components: []
    }
  },
  props: {
    // id: {
    //   default: '',
    //   type: String
    // }
  },
  components: {
    BaseTheme,
    Table
  },
  // beforeRouteEnter () {
  // },
  // beforeRouteUpdate () {
  // },
  // beforeRouteLeave () {
  // },
  beforeCreate () {
  },
  created () {
    apiRouter().then(({data}) => {
      this.components = data
    })
  },
  beforeMount () {
  },
  mounted () {
  },
  beforeUpdate () {
  },
  updated () {
  },
  beforeDestroy () {
  },
  destroyed () {
  },
  watch: {},
  computed: {},
  directives: {},
  filter: {},
  methods: {
    // 方法调用不需要注释，方法定义需要注释
    // 方法注释规则 /** */
  }
}
</script>

<style scoped>

</style>



