<template>
  <div>
    <TableBase v-bind="tableOptions" ref="table"></TableBase>
    <el-button @click="refresh">刷新当前页</el-button>
    <el-button @click="getSelectData">获取当前页选中数据</el-button>
  </div>
</template>

<script>
// import
import TableBase from '@/components/table/TableBase'
import {apiTable} from '@/api'
export default {
  name: 'Table',
  data () {
    return {
      // 定义的每个data 属性必须有注释
      // 注释规则： //
      // 表格参数
      tableOptions: {
        api: apiTable,
        // query: {
        //   a: 1,
        //   b: 2
        // },
        // staticData: [
        //   {
        //     date: '2016-05-02',
        //     name: '王小虎',
        //     address: '上海市普陀区金沙江路 1518 弄'
        //   },
        //   {
        //     date: '2016-05-04',
        //     name: '王小虎',
        //     address: '上海市普陀区金沙江路 1517 弄'
        //   },
        //   {
        //     date: '2016-05-01',
        //     name: '王小虎',
        //     address: '上海市普陀区金沙江路 1519 弄'
        //   },
        //   {
        //     date: '2016-05-03',
        //     name: '王小虎',
        //     address: '上海市普陀区金沙江路 1516 弄'
        //   },
        //   {
        //     date: '2016-05-03',
        //     name: '王小虎',
        //     address: '上海市普陀区金沙江路 1516 弄'
        //   }
        // ],
        columns: [
          {
            attr: {
              prop: 'checkbox',
              type: 'selection',
              label: '多选',
              width: '80'
            }
          },
          {
            attr: {
              prop: 'num',
              type: 'index',
              label: '序号',
              width: '80'
            }
          },
          {
            attr: {
              prop: 'e',
              type: 'expand',
              label: '展开',
              width: '80'
            }
          },
          {
            attr: {
              prop: 'date',
              label: '日期',
              width: '180'
            }
          },
          {
            attr: {
              prop: 'name',
              label: '姓名',
              width: '180',
              renderHeader (h, {column, $index}) {
                // console.log('h:', h)
                // console.log('column:', column)
                // console.log('$index:', $index)
                return column.label
              },
              formatter (row, column, cellValue, index) {
                // console.log('row:', row)
                // console.log('column:', column)
                // console.log('cellValue:', cellValue)
                // console.log('index:', index)
                return cellValue
              }
            }
          },
          {
            attr: {
              prop: 'address',
              label: '地址'
            },
            template: [
              {
                el: 'el-input',
                attr: {}
              }
            ]
          },
          {
            attr: {
              prop: 'operation',
              label: '操作',
              formatter: (row, column, cellValue, index) => {

              }
            },
            template: [
              {
                el: 'el-button',
                attr: {
                },
                attrByRow: {
                  disabled: function (index, row) {
                    if (row.date === '2016-05-04') {
                      return true
                    }
                  }
                },
                events: {
                  click (...arr) {
                    console.log('arr:', arr)
                  }
                },
                content: '编辑',
                handle (...arr) {
                  console.log('arr:', arr)
                }
              },
              {
                el: 'el-button',
                attr: {},
                // events: {
                //   click (...arr) {
                //     console.log('arr:', arr)
                //   }
                // },
                content: '保存'
              }
            ]
          }
        ],
        pageSize: 3
      }
    }
  },
  props: {
    // id: {
    //   default: '',
    //   type: String
    // }
  },
  components: {
    TableBase
  },
  // beforeRouteEnter () {
  // },
  // beforeRouteUpdate () {
  // },
  // beforeRouteLeave () {
  // },
  beforeCreate () {
  },
  created () {
  },
  beforeMount () {
  },
  mounted () {
  },
  beforeUpdate () {
  },
  updated () {
  },
  beforeDestroy () {
  },
  destroyed () {
  },
  watch: {},
  computed: {},
  directives: {},
  filter: {},
  methods: {
    // 方法调用不需要注释，方法定义需要注释
    // 方法注释规则 /** */
    /**
     * @desc 刷新
     */
    refresh () {
      this.$refs.table.refresh()
    },
    /**
     * @desc 获取选中数据
     */
    getSelectData () {
      let data = this.$refs.table.getSelectData()
      console.log(data)
    }
  }
}
</script>

<style scoped>

</style>



