<!--公用组件：数据表格
 /**
  * @desc BaseTheme
  * @module components/theme/BaseTheme
  * @desc 一个皮肤选择器
  * @author glk
  * @date 2020年02月13日10:00:00
  * @example 调用示例
  *  <BaseTheme></BaseTheme>
  */
-->
<template>
  <el-select v-model="value" placeholder="请选择">
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value"
    >
    </el-option>
  </el-select>
</template>

<script>
// import
export default {
  name: 'BaseTheme',
  data () {
    return {
      // 定义的每个data 属性必须有注释
      // 注释规则： //
      // 可选皮肤的下拉框参数
      options: [{
        value: 'default',
        label: '默认皮肤'
      }, {
        value: 'theme1',
        label: '皮肤一'
      }],
      // 默认皮肤
      value: 'default'
    }
  },
  props: {
    id: {
      default: '',
      type: String
    }
  },
  components: {},
  // beforeRouteEnter () {
  // },
  // beforeRouteUpdate () {
  // },
  // beforeRouteLeave () {
  // },
  beforeCreate () {
  },
  created () {
    // 获取本地保存的皮肤
    let theme = localStorage.getItem('base_theme')
    // 如果本地有保存，且与默认皮肤不同，就替换掉当前皮肤
    if (theme && theme !== this.value) {
      this.value = theme
    } else {
      // 显示页面
      this.showPage()
    }
  },
  beforeMount () {
  },
  mounted () {
  },
  beforeUpdate () {
  },
  updated () {
  },
  beforeDestroy () {
  },
  destroyed () {
  },
  watch: {
    value (val) {
      // 当切换皮肤的选择时，修改皮肤的引入
      this.changeTheme(val)
      // 修改本地保存的皮肤选项
      localStorage.setItem('base_theme', val)
    }
  },
  computed: {},
  directives: {},
  filter: {},
  methods: {
    // 方法调用不需要注释，方法定义需要注释
    // 方法注释规则 /** */
    /**
     * @desc 修改皮肤的引入
     * @param {String} val 皮肤的值
     */
    changeTheme (val) {
      // 获取引入皮肤的 link 文件
      let linkEl = document.getElementById('base_theme')
      if (!linkEl) {
        return
      }
      // 修改 link 文件的引入路径
      let href = linkEl.href
      if (new RegExp(/(theme\/\S+\/index)/).test(href)) {
        href = href.replace(RegExp.$1, `theme/${val}/index`)
      }
      linkEl.href = href
      // 显示页面
      this.showPage()
    },
    /**
     * @desc 显示页面（为了避免初始皮肤设置出现的闪烁，在皮肤切换完之前，隐藏页面）
     */
    showPage () {
      this.$common.removeClass(document.body, 'hide')
    }
  }
}
</script>

<style scoped>

</style>



